import { useRef, useState, useEffect } from 'react'

export default function Home(){
  const canvasRef = useRef(null)
  const imageRef = useRef(null)
  const fileInputRef = useRef(null)

  const [image, setImage] = useState(null)
  const [topText, setTopText] = useState('')
  const [bottomText, setBottomText] = useState('')
  const [fontSize, setFontSize] = useState(48)
  const [fontFamily, setFontFamily] = useState('Impact')
  const [textColor, setTextColor] = useState('#FFFFFF')
  const [strokeColor, setStrokeColor] = useState('#000000')
  const [strokeWidth, setStrokeWidth] = useState(6)
  const [scaleMode, setScaleMode] = useState('contain')
  const [width, setWidth] = useState(800)
  const [height, setHeight] = useState(800)

  const [topPos, setTopPos] = useState({x: 20, y: 60})
  const [bottomPos, setBottomPos] = useState({x: 20, y: 740})

  useEffect(()=>{
    draw()
  },[image, topText, bottomText, fontSize, fontFamily, textColor, strokeColor, strokeWidth, width, height, topPos, bottomPos, scaleMode])

  function handleFile(e){
    const f = e.target.files?.[0]
    if(!f) return
    const url = URL.createObjectURL(f)
    const img = new Image()
    img.onload = ()=>{
      imageRef.current = img
      const max = 1000
      let w = img.width
      let h = img.height
      if(Math.max(w,h) > max){
        const ratio = max / Math.max(w,h)
        w = Math.round(w * ratio)
        h = Math.round(h * ratio)
      }
      setWidth(w)
      setHeight(h)
      setImage(url)
    }
    img.onerror = ()=>{ alert('Could not read image') }
    img.src = url
  }

  function draw(){
    const canvas = canvasRef.current
    if(!canvas) return
    const ctx = canvas.getContext('2d')
    canvas.width = width
    canvas.height = height
    ctx.clearRect(0,0,canvas.width,canvas.height)
    ctx.fillStyle = '#0b1220'
    ctx.fillRect(0,0,canvas.width,canvas.height)

    if(imageRef.current){
      const img = imageRef.current
      if(scaleMode === 'cover'){
        const r = Math.max(canvas.width / img.width, canvas.height / img.height)
        const w = img.width * r
        const h = img.height * r
        const x = (canvas.width - w)/2
        const y = (canvas.height - h)/2
        ctx.drawImage(img, x, y, w, h)
      } else {
        const r = Math.min(canvas.width / img.width, canvas.height / img.height)
        const w = img.width * r
        const h = img.height * r
        const x = (canvas.width - w)/2
        const y = (canvas.height - h)/2
        ctx.drawImage(img, x, y, w, h)
      }
    } else {
      ctx.fillStyle = '#0f1724'
      ctx.fillRect(0,0,canvas.width,canvas.height)
      ctx.fillStyle = '#9aa4b2'
      ctx.font = '20px system-ui'
      ctx.textAlign = 'center'
      ctx.fillText('Upload an image to start', canvas.width/2, canvas.height/2)
    }

    ctx.textAlign = 'center'
    ctx.lineJoin = 'round'
    ctx.miterLimit = 2

    const drawMemeText = (text, pos)=>{
      if(!text) return
      const maxWidth = canvas.width - 40
      ctx.font = `bold ${fontSize}px ${fontFamily}, Impact, sans-serif`
      ctx.fillStyle = textColor
      ctx.strokeStyle = strokeColor
      ctx.lineWidth = Math.max(1, strokeWidth * (fontSize/48))

      const words = text.split(' ')
      const lines = []
      let cur = ''
      for(const w of words){
        const test = cur ? cur + ' ' + w : w
        const measure = ctx.measureText(test).width
        if(measure > maxWidth && cur){
          lines.push(cur)
          cur = w
        } else {
          cur = test
        }
      }
      if(cur) lines.push(cur)

      const lineHeight = fontSize * 1.05
      let startY = pos.y - ((lines.length-1)/2) * lineHeight
      for(let i=0;i<lines.length;i++){
        const y = startY + i*lineHeight
        ctx.strokeText(lines[i].toUpperCase(), pos.x + 0.5, y + 0.5)
        ctx.fillText(lines[i].toUpperCase(), pos.x, y)
      }
    }

    drawMemeText(topText, topPos)
    drawMemeText(bottomText, bottomPos)
  }

  function download(){
    const canvas = canvasRef.current
    if(!canvas) return
    const link = document.createElement('a')
    link.download = 'meme.png'
    link.href = canvas.toDataURL('image/png')
    link.click()
  }

  function centerTop(){ setTopPos(p=>({x:width/2, y: Math.max(40, fontSize + 12)})) }
  function centerBottom(){ setBottomPos(p=>({x:width/2, y: Math.min(height - 20, height - fontSize)})) }
  function clearImage(){ setImage(null); imageRef.current = null }

  return (
    <div className="container">
      <div className="card">
        <div className="header">
          <div className="h1">Meme Generator — Next.js + Canvas</div>
          <div style={{marginLeft:'auto'}} className="small">Static-ready · client-only · exportable</div>
        </div>

        <div className="controls">
          <div className="left">
            <div className="canvas-wrap">
              <canvas ref={canvasRef} style={{width:'100%',height:'auto',maxHeight:'80vh',borderRadius:8}} />
            </div>

            <div className="canvas-controls">
              <div style={{display:'flex',gap:8}}>
                <button className="button" onClick={()=>fileInputRef.current.click()}>Upload Image</button>
                <input ref={fileInputRef} type="file" accept="image/*" onChange={handleFile} style={{display:'none'}} />
                <button className="button" onClick={download}>Download PNG</button>
                <button className="button" onClick={clearImage} style={{background:'#ef4444'}}>Clear</button>
              </div>
              <div style={{display:'flex',gap:8,alignItems:'center'}}>
                <label style={{fontSize:13,color:'#9aa4b2'}}>Canvas</label>
                <input type="number" value={width} onChange={e=>setWidth(Math.max(100,Number(e.target.value)||300))} style={{width:80,padding:8,borderRadius:8}} />
                <input type="number" value={height} onChange={e=>setHeight(Math.max(100,Number(e.target.value)||300))} style={{width:80,padding:8,borderRadius:8}} />
                <select value={scaleMode} onChange={e=>setScaleMode(e.target.value)} style={{padding:8,borderRadius:8}}>
                  <option value="contain">Contain</option>
                  <option value="cover">Cover</option>
                </select>
              </div>
            </div>

            <div className="footer">Tip: use UPPERCASE for classic meme look. Adjust stroke width for readability.</div>

          </div>

          <div className="right">
            <div className="card" style={{padding:12}}>
              <div className="input-row">
                <label>Top Text</label>
                <input type="text" value={topText} onChange={e=>setTopText(e.target.value)} placeholder="e.g. WHEN YOU..." />
              </div>

              <div className="input-row">
                <label>Bottom Text</label>
                <input type="text" value={bottomText} onChange={e=>setBottomText(e.target.value)} placeholder="e.g. ...REALIZE" />
              </div>

              <div className="input-row">
                <label>Font Size</label>
                <input type="number" value={fontSize} onChange={e=>setFontSize(Math.max(10,Number(e.target.value)||24))} />
              </div>

              <div className="input-row">
                <label>Font</label>
                <select value={fontFamily} onChange={e=>setFontFamily(e.target.value)}>
                  <option>Impact</option>
                  <option>Arial</option>
                  <option>Helvetica</option>
                  <option>Comic Sans MS</option>
                </select>
              </div>

              <div className="input-row">
                <label>Text Color</label>
                <input type="color" value={textColor} onChange={e=>setTextColor(e.target.value)} />
              </div>

              <div className="input-row">
                <label>Stroke</label>
                <input type="color" value={strokeColor} onChange={e=>setStrokeColor(e.target.value)} />
                <input type="number" value={strokeWidth} onChange={e=>setStrokeWidth(Math.max(0,Number(e.target.value)||1))} style={{width:80}} />
              </div>

              <div className="input-row">
                <label>Top X</label>
                <input type="number" value={Math.round(topPos.x)} onChange={e=>setTopPos(p=>({x: Number(e.target.value)||0, y: p.y}))} />
              </div>
              <div className="input-row">
                <label>Top Y</label>
                <input type="number" value={Math.round(topPos.y)} onChange={e=>setTopPos(p=>({x: p.x, y: Number(e.target.value)||0}))} />
              </div>
              <div style={{display:'flex',gap:8,marginBottom:8}}>
                <button className="button" onClick={centerTop}>Center Top</button>
              </div>

              <div className="input-row">
                <label>Bottom X</label>
                <input type="number" value={Math.round(bottomPos.x)} onChange={e=>setBottomPos(p=>({x: Number(e.target.value)||0, y: p.y}))} />
              </div>
              <div className="input-row">
                <label>Bottom Y</label>
                <input type="number" value={Math.round(bottomPos.y)} onChange={e=>setBottomPos(p=>({x: p.x, y: Number(e.target.value)||0}))} />
              </div>
              <div style={{display:'flex',gap:8}}>
                <button className="button" onClick={centerBottom}>Center Bottom</button>
              </div>

              <div className="instructions">Adjust the X/Y to move text. Use the Font Size and Stroke to improve contrast on busy images. When happy, click <strong>Download PNG</strong>.</div>
            </div>

            <div style={{marginTop:12}} className="card small">This app runs fully in the browser. To deploy static: run `next build` then `next export` (or deploy to Vercel).</div>
          </div>
        </div>

      </div>
    </div>
  )
}